@use "../../../variables" as var;

.planCard {
  position: relative;
  width: 100%;
  background: var.$color-content-background;
  border-radius: 20px;
  padding: 20px;
  overflow: hidden;
}
.planBar {
  background-color: var.$color-primary-yellow;
  width: fit-content;
  position: absolute;
  left: 0;
  top: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-transform: capitalize;
  color: #000000;
  padding: 5px 10px;
  border-bottom-right-radius: 5px;
}
.profitDetail {
  margin-top: 30px;
  width: 100%;
  :global(.ant-input) {
    background: #151414 !important;
    box-shadow: none;
    border: none !important;
    height: 50px;
    font-size: 30px;
    font-weight: 700;
    color: white;
  }
}
.detailTitle {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: capitalize;
  color: white;
  opacity: 0.5;
  text-align: left;
  margin-bottom: 10px;
}

.detailValue {
  display: block;
  font-weight: 700;
  font-size: 45px;
  line-height: 45px;
  // text-transform: capitalize;
  text-align: left;
  margin-bottom: 0;
  color: var.$color-primary-yellow;
}

.getBnb {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}
.planButton {
  margin-top: 10px;
  width: 100%;
  background-color: var.$color-primary-yellow;
  border-radius: 8px;
  padding: 15px 0px;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  text-transform: capitalize;

  color: #151414;
}

.blue {
  .planBar {
    background-color: var.$color-primary-blue;
  }

  .detailValue {
    color: var.$color-primary-blue;
  }

  .button {
    background-color: var.$color-primary-blue;
  }
}
.footNote {
  margin: 0;
  color: white;
  opacity: 0.5;
  margin-top: 10px;
}
